import * as Yup from 'yup';
import { nameRegex } from '../regex';

export const userDataSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞")
    .max(20, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞")
    .trim()
    .matches(nameRegex, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è –∫–∏—Ä–∏–ª–∏—Ü–µ—é, –±—É–¥—å –ª–∞—Å–∫–∞")
    .required("–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞"),
  lastName: Yup.string()
    .min(2, '–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î –ø—Ä—ñ–∑–≤–∏—â–µ, –±—É–¥—å –ª–∞—Å–∫–∞')
    .max(30, '–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î –ø—Ä—ñ–∑–≤–∏—â–µ, –±—É–¥—å –ª–∞—Å–∫–∞')
    .trim()
    .matches(nameRegex, '–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î –ø—Ä—ñ–∑–≤–∏—â–µ –∫–∏—Ä–∏–ª–∏—Ü–µ—é, –±—É–¥—å –ª–∞—Å–∫–∞')
    .required("–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞"),
  patronymic: Yup.string()
    .min(2, "–í–≤–µ–¥—ñ—Ç—å —ñ–º'—è –ø–æ –±–∞—Ç—å–∫–æ–≤—ñ, –±—É–¥—å –ª–∞—Å–∫–∞")
    .max(30, "–í–≤–µ–¥—ñ—Ç—å —ñ–º'—è –ø–æ –±–∞—Ç—å–∫–æ–≤—ñ, –±—É–¥—å –ª–∞—Å–∫–∞")
    .trim()
    .matches(nameRegex, "–í–≤–µ–¥—ñ—Ç—å —ñ–º'—è –ø–æ –±–∞—Ç—å–∫–æ–≤—ñ –∫–∏—Ä–∏–ª–∏—Ü–µ—é, –±—É–¥—å –ª–∞—Å–∫–∞"),
});
